import { useState } from "react";
import { Link } from "react-router-dom";

import Profile from "../Profile/Profile";

import styles from "./Sidebar.module.css";

const SidebarItem = [
  {
    name: "Trang chủ",
    icon: "fa-solid fa-house",
    path: "",
    role: [1]
  },
  {
    name: "Cửa hàng của tôi",
    icon: "fa-solid fa-shop",
    path: "shop",
    role: [1]
  },
  {
    name: "Danh mục",
    icon: "bx fa-solid fa-dice-d20",
    path: "category", 
    role: [1]
  },
  {
    name: "Nhà phân phối",
    icon: "fa-solid fa-industry",
    path: "manufacturers",
    role: [1]
  },
  {
    name: "Người dùng",
    icon: "fa-solid fa-users",
    path: "",
    role: [0]
  },
  {
    name: "Đơn hàng",
    icon: "fa-solid fa-cart-shopping",
    path: "orders",
    role: [1]
  }
]

function Sidebar({ handleHideSidebar, isHide, user }) {

  return (
    <div className={`${styles.container} ${isHide ? styles.hide : ""}`}>
      <div className={styles.header_content}>
        <div className={styles.hero_title}>Trang quản trị</div>
        <div className={styles.hero_logo} onClick={() => handleHideSidebar()}>
          <i className="fa-solid fa-bars" />
        </div>
      </div>
      <div className={styles.body_content}>
        <ul className={styles.nav_list}>
          {SidebarItem.map((item, index) => {
            if (item.role.includes(user.USER_ROLE)) {
              return (
                <li key={index}>
                  <Link to={item.path} className={styles.nav_item_link}>
                    <i className={item.icon} />
                    <div className={styles.item_name_wrapper}>
                      <span className={styles.item_name}>{item.name}</span>
                    </div>
                  </Link>
                </li>
              )
            }
          })}
        </ul>
      </div>
      <div className={styles.footer_content}>
        <Profile isHide={isHide} user={user} />
      </div>
    </div>
  );
}

export default Sidebar;
